import React from "react";
import { IoWarning } from "react-icons/io5";
// import { MdInfoOutline } from "react-icons/md";
import { NavLink } from "react-router-dom";

type NoSessionProps = {
  title?: string;
  description?: string;
  content?: any;
};

const NoSession: React.FC<NoSessionProps> = ({
  title = "No session available",
  description = "Please Wait for the system to publish the sessions or contact the administrator",
  content = undefined,
}) => {
  return (
    <div className="w-full my-5">
      <section className="py-10 flex flex-col items-center rounded border border-gray-300 bg-white text-2xl font-medium text-yellow-800 animate__animated animate__pulse">
        <div className="flex flex-col items-center">
          <IoWarning className="w-32 h-32" />
          {description}
        </div>
        {content && content}
        {false && (
          <div className="text-center group text-sm -mb-3 mt-5">
            <NavLink
              to="/apps-page"
              className="border border-rounded-md bg-gray-100 text-gray-900 rounded border-gray-100 group-hover:bg-gray-50 group-hover:border-gray-400 px-2 py-1"
            >
              Back to App
            </NavLink>
          </div>
        )}
      </section>
    </div>
  );
};

export default React.memo(NoSession);
