export const FC_GetPositionsByInstitution = (
  institution_id: string,
  callback: (status: boolean, msg: string, data?: PositionAPI[]) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<PositionAPI[]>(
        `${API.APPLICATION}/position/instituteposition/${institution_id}`
      );
      dispatch<GetPositionsAction>({
        type: ActionTypes.GET_POSTIONS,
        payload: res.data,
      });
      callback(false, "", res.data);
    } catch (error) {
      console.log({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};
