import axios from "axios";
import React, { Component, Fragment } from "react";
import { BiDetail } from "react-icons/bi";
import { FaRegCheckCircle } from "react-icons/fa";
import { HiOutlineDuplicate, HiOutlineDocumentSearch } from "react-icons/hi";
import { ImFilesEmpty } from "react-icons/im";
import { IoDocumentOutline } from "react-icons/io5";
import { MdRemoveCircleOutline, MdInfo } from "react-icons/md";
import { RiListSettingsLine } from "react-icons/ri";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css"; // ES6
import { connect } from "react-redux";
import { Auth, reAssessApplication, PushPopupAlert } from "../../actions";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
} from "../../actions/document";
import { PopupAlertData } from "../../components/Alert/Popup";
import { ApplicationStates } from "../../interfaces/application";
import { GetDocTitle } from "../../interfaces/document_validation";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import { capitalizeFirstLetter } from "../../shared/dataTest";
import {
  ApplicationAssessmentInterface,
  CandidateDetailsInterface_old,
  PositionRequirementInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import DocumentLoading from "../DocumentValidation/DocumentApplChecking/DocumentLoading";
import DisplayCompetence from "../Positions/DisplayCompetence";

const countByStatus = (
  userDocs: CandidateDetailsInterface_old[],
  docStatus: "waiting" | "approved" | "rejected"
): number => {
  let result: CandidateDetailsInterface_old[] = userDocs;
  result = userDocs.filter(
    (details) => details.validation_status === docStatus && details.files
  );
  return result.length;
};

interface AppProps {
  auth: Auth;
  documentCategories: DocumentReducerInterface;
  thisApplication: ApplicationAssessmentInterface;
  setThisApplication: (data: ApplicationAssessmentInterface | null) => void;
  reAssessApplication: Function;
  setDoc: (data: CandidateDetailsInterface_old | null) => void;
  setDocTitle: (data: string) => void;
  setDocSubTitle: (data: string) => void;
  getDocumentFields: Function;
  setModalState: (status: boolean) => void;
  setApplication: (data: ApplicationAssessmentInterface | null) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppStates {
  loading: boolean;
  tabsController: "waiting" | "approved" | "rejected";
  tabsCategory: "profile" | "requirements";
  override: "" | ApplicationStates;
  loadingRequest: boolean;

  formError: {
    element: string;
    msg: string;
  } | null;
  reason: string;
}

class UserDocuments extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      tabsController: "approved",
      tabsCategory: "requirements",
      override: "",
      loadingRequest: false,
      formError: null,
      reason: "",
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };

  setTabsController = (value: "waiting" | "approved" | "rejected") => {
    this.setState({ tabsController: value });
  };

  setTabsCategory = (value: "profile" | "requirements") => {
    this.setState({ tabsCategory: value });
  };

  setOverride = (value: "" | ApplicationStates) => {
    this.setState({ override: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  setReason = (value: string) => {
    this.setState({ reason: value });
  };

  componentDidMount = () => {
    if (!this.props.documentCategories.documentFields && this.state.loading) {
      getDocumentFields();
      this.setLoading(false);
    }
    if (this.props.documentCategories.documentFields && this.state.loading) {
      this.setLoading(false);
    }
  };

  isRequired = (
    requirements: PositionRequirementInterface[],
    filterValue: string
  ): boolean => {
    return requirements.filter(
      (item) => item.user_detail_category_id === filterValue
    ).length > 0
      ? true
      : false;
  };

  countById = (
    userDocs: CandidateDetailsInterface_old[],
    filterType: "docCategory" | "docSubCategory" | "docStatus",
    filterValue: string
  ): number => {
    let result: CandidateDetailsInterface_old[] = userDocs;
    if (filterType === "docCategory") {
      result = userDocs.filter(
        (details) =>
          details.category_id === filterValue &&
          details.files &&
          details.files.length > 0
      );
    } else if (filterType === "docSubCategory") {
      result = userDocs.filter(
        (details) =>
          details.sub_category_id === filterValue &&
          details.files &&
          details.files.length > 0
      );
    }
    // if (docStatus === "approved") {
    //   result = userDocs.filter(
    //     (details) => details.validation_status !== "waiting"
    //   );
    // }
    return result.length;
  };

  overrideDecision = async () => {
    if (this.state.reason === "") {
      return this.setFormError({
        element: "comment",
        msg: "Please fill the reason of override",
      });
    }

    this.setLoadingRequest(true);

    try {
      const res = await axios.patch(
        `${API.APPLICATION}/apply/tolerateappl/${this.props.thisApplication.application_id}`,
        {
          user_id: this.props.auth.user.user_id,
          email: this.props.thisApplication.email,
          phone_number: this.props.thisApplication.phone_number,
          comment: this.state.reason,
          application_status: this.state.override,
        }
      );

      if (res.data) {
        this.props.reAssessApplication({
          application_id: this.props.thisApplication.application_id,
          application_status: this.state.override,
        });
        this.setOverride("");
        this.setReason("");
        this.props.setDoc(null);
        this.props.setModalState(false);
        this.props.setApplication(null);
        this.props.setDocTitle("");
        this.props.setDocSubTitle("");

        this.props.PushPopupAlert({
          title: `Overriding application done successfully`,
          theme: PopupAlertTheme.success,
        });
      }
    } catch (error) {
      console.error({ code_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      // errorToText(error);
    }
    this.setLoadingRequest(false);
  };

  render() {
    return (
      <div className="bg-gray-100 py-2 px-4">
        {/* tabs status */}
        <div className="w-full mx-auto mt-1 bg-white flex items-center justify-evenly border border-gray-200 rounded">
          <section
            className="cursor-pointer"
            onClick={() => this.setTabsCategory("requirements")}
          >
            <div
              className={`flex items-center py-4 px-2 text-gray-700 gap-1 ${
                this.state.tabsCategory === "requirements" &&
                "border-b-4 border-gray-700"
              }`}
            >
              <RiListSettingsLine className="w-6 h-6" />
              <span>Position Requirements</span>
            </div>
          </section>
        </div>
        {/* end tabs status */}

        {/* ------------------------------------------------------------------------- */}

        <div className="h-full overflow-y-auto">
          {/* ================================================================= */}
          <div className="w-full flex flex-col md:items-start overflow-y-auto">
            {/* ======================================================= */}
            {/* POSITION for position info */}
            {this.state.tabsCategory === "requirements" && (
              <section className="h-full w-full mx-auto pt-2">
                {/* position description */}
                <div className="flex items-center font-semibold uppercase text-xl">
                  <HiOutlineDuplicate className="font-semibold w-6 h-6 mr-1" />
                  Position Description
                </div>
                <div className="bg-white border border-gray-200 rounded my-3">
                  <section className="border-gray-200 -mt-1 pt-2 mb-5">
                    <div className="flex flex-col mx-2 gap-1 my-1">
                      <span className="text-sm text-gray-500 uppercase whitespace-no-wrap">
                        Title:
                      </span>
                      <p className="font-semibold">
                        {this.props.thisApplication.position.position_title}
                      </p>
                    </div>

                    <div className="flex flex-col mx-2 gap-1 my-1">
                      <span className="text-sm text-gray-500 uppercase whitespace-no-wrap">
                        Description:
                      </span>
                      <p className="font-medium">
                        <DisplayCompetence
                          competence={
                            this.props.thisApplication.position.description
                          }
                          displayType="text-one"
                        />
                      </p>
                    </div>
                  </section>
                </div>
                {/* end position description */}

                {/* position Requirements */}
                <div className="my-3">
                  <div className="flex items-center font-semibold uppercase text-xl">
                    <BiDetail className="font-semibold w-6 h-6 mr-1" />
                    Position Requirements
                  </div>

                  <section className="bg-white border rounde border-gray-200 mt-2 pt-2 mb-2">
                    {this.props.documentCategories.documentCategory &&
                    this.props.documentCategories.documentCategory.length >
                      0 ? (
                      this.props.documentCategories.documentCategory.map(
                        (category, index) => (
                          <Fragment
                            key={index + category.user_detail_category_id}
                          >
                            {this.isRequired(
                              this.props.thisApplication.position.requirements,
                              category.user_detail_category_id
                            ) &&
                              this.countById(
                                this.props.thisApplication.details,
                                "docCategory",
                                category.user_detail_category_id
                              ) > 0 && (
                                <section className="px-3">
                                  <section
                                    className={`bg-blue-100 text-blue-800 mb-1 hover:font-bold py-2 --font-bold px-2 text-2xl rounded-md flex items-center`}
                                  >
                                    <section className="flex-1">
                                      <span>{category.title}</span>
                                    </section>

                                    {/* {count > 0 ? ( */}
                                    <span className="px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full">
                                      {this.countById(
                                        this.props.thisApplication.details,
                                        "docCategory",
                                        category.user_detail_category_id
                                      )}
                                    </span>
                                    {/* ) : null} */}
                                  </section>

                                  {/* list of docs */}
                                  <section className="w-full h-full my-3">
                                    {/* doc card */}
                                    {this.props.thisApplication.details.filter(
                                      (itm) => itm.files && itm.files.length > 0
                                    ).length > 0 ? (
                                      <section className="animate__animated animate__fadeIn animate__faster max-h-96 overflow-y-auto">
                                        {this.state.loading ? (
                                          <DocumentLoading />
                                        ) : (
                                          this.props.thisApplication.details
                                            .filter(
                                              (itm) =>
                                                itm.files &&
                                                itm.files.length > 0
                                            )
                                            .map((item, index) => (
                                              <div
                                                key={index + item.doc_id}
                                                className="bg-white border group hover:bg-gray-100 border-gray-200 rounded mb-3 p-4 flex justify-between items-start cursor-pointer"
                                                onClick={() => {
                                                  this.props.setDoc(item);
                                                  this.props.setDocTitle(
                                                    item.category_id
                                                      ? this.props
                                                          .documentCategories
                                                          .documentCategory
                                                        ? this.props.documentCategories.documentCategory.filter(
                                                            (doc) =>
                                                              doc.user_detail_category_id ===
                                                              item.category_id
                                                          )[0].title
                                                        : item.title
                                                        ? item.title
                                                        : "No Category or Title Available"
                                                      : "No Category or Title Available"
                                                  );
                                                  this.props.setDocSubTitle(
                                                    item.sub_category_id
                                                      ? this.props
                                                          .documentCategories
                                                          .documentSubCategory
                                                        ? this.props.documentCategories.documentSubCategory.filter(
                                                            (c) =>
                                                              c.user_detail_subcategory_id ===
                                                              item.sub_category_id
                                                          )[0].title
                                                        : ""
                                                      : ""
                                                  );
                                                }}
                                              >
                                                {/* doc title */}
                                                <div>
                                                  <h2 className="text-balck font-semibold text-xl">
                                                    {
                                                      GetDocTitle(
                                                        this.props
                                                          .documentCategories,
                                                        item
                                                      ).main
                                                    }
                                                  </h2>
                                                  {/* status */}
                                                  <div className="flex items-center gap-2">
                                                    <span className="text-gray-500 italic">
                                                      Status:
                                                    </span>
                                                    <span
                                                      className={`text-${
                                                        item.validation_status ===
                                                        "waiting"
                                                          ? "yellow"
                                                          : item.validation_status ===
                                                            "approved"
                                                          ? "blue"
                                                          : "gray"
                                                      }-800`}
                                                    >
                                                      {item.validation_status ===
                                                      "waiting"
                                                        ? "Pending"
                                                        : item.validation_status ===
                                                          "approved"
                                                        ? "Approved"
                                                        : "Rejected"}
                                                    </span>
                                                  </div>
                                                  {/* end status */}
                                                </div>
                                                {/* end doc title */}
                                                <button className="bg-blue-50 p-2 text-blue-600 font-medium group-hover:bg-blue-200 flex rounded items-center space-x-2 focus:outline-none">
                                                  <IoDocumentOutline className="w-5 h-5" />
                                                  View doc
                                                </button>
                                              </div>
                                            ))
                                        )}
                                      </section>
                                    ) : (
                                      <div className="bg-white border border-gray-200 rounded mb-3 p-4 text-gray-400 text-center">
                                        <ImFilesEmpty className="w-28 h-28 mx-auto mb-3" />
                                        <span className="text-2xl font-mono font-semibold tracking-tighter">
                                          No Data Found
                                        </span>
                                      </div>
                                    )}

                                    {/* end doc card */}
                                  </section>
                                  {/* end list of docs */}
                                </section>
                              )}
                          </Fragment>
                        )
                      )
                    ) : (
                      <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
                        <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
                        <span>No position requirements</span>
                      </div>
                    )}
                  </section>
                </div>
                {/* end position Requirements */}
              </section>
            )}
            {/* end POSITION for position info */}

            {/* ==================================================================== */}
            {/* PROFILE MANAGEMENT */}
            {this.state.tabsCategory === "profile" && (
              <section className="w-full h-full my-3 mx-auto">
                {/* tabs status */}
                <div className="w-full bg-white flex items-center justify-evenly border border-gray-200 rounded">
                  <section
                    className="cursor-pointer"
                    onClick={() => this.setTabsController("approved")}
                  >
                    <div
                      className={`flex items-center py-4 px-2 text-blue-700 gap-1 ${
                        this.state.tabsController === "approved" &&
                        " border-b-4 border-blue-700"
                      }`}
                    >
                      <FaRegCheckCircle className="w-6 h-6" />
                      <span>Approved</span>
                      <span>
                        (
                        {countByStatus(
                          this.props.thisApplication.details,
                          "approved"
                        )}
                        )
                      </span>
                    </div>
                  </section>
                  <section
                    className="cursor-pointer"
                    onClick={() => this.setTabsController("rejected")}
                  >
                    <div
                      className={`flex items-center py-4 px-2 text-gray-700 gap-1 ${
                        this.state.tabsController === "rejected" &&
                        "border-b-4 border-gray-700"
                      }`}
                    >
                      <MdRemoveCircleOutline className="w-6 h-6" />
                      <span>Rejected</span>
                      <span>
                        (
                        {countByStatus(
                          this.props.thisApplication.details,
                          "rejected"
                        )}
                        )
                      </span>
                    </div>
                  </section>
                  <section
                    className="cursor-pointer"
                    onClick={() => this.setTabsController("waiting")}
                  >
                    <div
                      className={`flex items-center py-4 px-2 text-yellow-800 gap-1 ${
                        this.state.tabsController === "waiting" &&
                        "border-b-4 border-yellow-800"
                      }`}
                    >
                      <MdInfo className="w-6 h-6" />
                      <span>Pending</span>
                      <span>
                        (
                        {countByStatus(
                          this.props.thisApplication.details,
                          "waiting"
                        )}
                        )
                      </span>
                    </div>
                  </section>
                </div>
                {/* end tabs status */}

                {/* ============================================================================= */}

                {/* list of docs */}
                <section className="w-full h-full my-3">
                  {/* doc card */}
                  {this.props.thisApplication.details.filter(
                    (itm) =>
                      itm.validation_status === this.state.tabsController &&
                      itm.files.length > 0
                  ).length > 0 ? (
                    this.props.thisApplication.details
                      .filter(
                        (itm) =>
                          itm.validation_status === this.state.tabsController &&
                          itm.files.length > 0
                      )
                      .map((item, index) => (
                        <div
                          key={index + item.doc_id}
                          className="bg-white border border-gray-200 rounded mb-3 p-4 flex justify-between items-start"
                        >
                          {/* doc title */}
                          <div>
                            <h2 className="text-balck font-semibold text-xl">
                              {item.sub_category_id
                                ? this.props.documentCategories
                                    .documentSubCategory &&
                                  this.props.documentCategories.documentSubCategory.filter(
                                    (c) =>
                                      c.user_detail_subcategory_id ===
                                      item.sub_category_id
                                  )[0].title
                                : item.category_id
                                ? this.props.documentCategories
                                    .documentCategory &&
                                  this.props.documentCategories.documentCategory.filter(
                                    (doc) =>
                                      doc.user_detail_category_id ===
                                      item.category_id
                                  )[0].title
                                : item.title
                                ? item.title
                                : "No Category or Title Available"}
                            </h2>
                            {/* status */}
                            <div className="flex items-center gap-2">
                              <span className="text-gray-500 italic">
                                Status:
                              </span>
                              <span
                                className={`text-${
                                  item.validation_status === "waiting"
                                    ? "yellow"
                                    : item.validation_status === "approved"
                                    ? "blue"
                                    : "gray"
                                }-800`}
                              >
                                {item.validation_status === "waiting"
                                  ? "Pending"
                                  : item.validation_status === "approved"
                                  ? "Approved"
                                  : "Rejected"}
                              </span>
                            </div>
                            {/* end status */}
                          </div>
                          {/* end doc title */}
                          <button
                            className="bg-blue-600 p-2 text-white flex rounded items-center space-x-2 h-max focus:outline-none"
                            onClick={() => {
                              // setDoc(item);
                              // setDocTitle(
                              //   item.category_id
                              //     ? documentCategories.documentCategory.filter(
                              //         (doc) =>
                              //           doc.user_detail_category_id === item.category_id
                              //       )[0].title
                              //     : item.title
                              //     ? item.title
                              //     : "No Category or Title Available"
                              // );
                              // setDocSubTitle(
                              //   item.sub_category_id
                              //     ? documentCategories.documentSubCategory.filter(
                              //         (c) =>
                              //           c.user_detail_subcategory_id ===
                              //           item.sub_category_id
                              //       )[0].title
                              //     : ""
                              // );
                              // setDocIndex(index);
                            }}
                          >
                            <IoDocumentOutline className="w-5 h-5" />
                            View doc
                          </button>
                        </div>
                      ))
                  ) : (
                    <div className="bg-white border border-gray-200 rounded mb-3 p-4 text-gray-400 text-center">
                      <ImFilesEmpty className="w-28 h-28 mx-auto mb-3" />
                      <span className="text-2xl font-mono font-semibold tracking-tighter">
                        No Data Found
                      </span>
                    </div>
                  )}

                  {/* end doc card */}
                </section>
                {/* end list of docs */}
              </section>
            )}
            {/* end PROFILE MANAGEMENT */}
          </div>
          {/* ================================================================= */}
          {/* footer buttons */}
          <section className="w-full bg-white mt-1 p-3 border border-gray-200 rounded">
            {this.state.override && (
              <div>
                <ReactQuill
                  value={this.state.reason}
                  placeholder="Reason of your decision"
                  className={`rounded w-full ${
                    this.state.formError !== null &&
                    this.state.formError.element === "comment"
                      ? "border border-red-500"
                      : "border border-gray-300"
                  }`}
                  onChange={this.setReason}
                />
                {this.state.formError !== null &&
                  this.state.formError.element === "comment" && (
                    <p className="text-red-600 mt-1">
                      The reason of you decision is required!
                    </p>
                  )}
              </div>
            )}
            <div className="py-3 mx-auto flex items-center justify-between">
              <div className="flex items-center px-3">
                <span className="mr-1 text-gray-600">Status:</span>
                <span
                  className={`text-xl ${
                    this.props.thisApplication.application_status === "rejected"
                      ? "text-red-700"
                      : this.props.thisApplication.application_status ===
                        "initiated"
                      ? "text-gray-700"
                      : "text-blue-700"
                  }`}
                >
                  {capitalizeFirstLetter(
                    this.props.thisApplication.application_status
                  )}
                </span>
              </div>
              <div className="mr-3">
                {this.state.override ? (
                  <div className="max-w-sm mx-auto p-1 flex items-center gap-2">
                    <button
                      type="button"
                      className="btn bg-gray-100 text-gray-600 hover:bg-gray-200 text-base font-semibold p-3 focus:outline-none"
                      onClick={() =>
                        !this.state.loadingRequest && this.setOverride("")
                      }
                    >
                      Cancel
                    </button>
                    <button
                      type="button"
                      className="btn btn-blue text-white text-base font-semibold p-3 focus:outline-none"
                      onClick={() =>
                        !this.state.loadingRequest && this.overrideDecision()
                      }
                    >
                      {this.state.loadingRequest ? "Loading..." : "Confirm"}
                    </button>
                  </div>
                ) : (
                  <div className="flex items-center gap-2">
                    {(this.props.thisApplication.application_status ===
                      "rejected" ||
                      this.props.thisApplication.application_status ===
                        "initiated") && (
                      <button
                        className="btn btn-blue focus:outline-none"
                        onClick={() =>
                          !this.state.loadingRequest &&
                          this.setOverride(ApplicationStates.Shortlisted)
                        }
                      >
                        Short List
                      </button>
                    )}
                    {(this.props.thisApplication.application_status ===
                      "shortlisted" ||
                      this.props.thisApplication.application_status ===
                        "initiated") && (
                      <button
                        className="btn bg-red-700 text-white focus:outline-none"
                        onClick={() =>
                          !this.state.loadingRequest &&
                          this.setOverride(ApplicationStates.Rejected)
                        }
                      >
                        Reject
                      </button>
                    )}
                  </div>
                )}
              </div>
            </div>
          </section>
          {/* end footer buttons */}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  document,
}: StoreState): {
  auth: Auth;
  documentCategories: DocumentReducerInterface;
} => {
  return { auth, documentCategories: document };
};

export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  reAssessApplication,
  getDocumentFields,
  PushPopupAlert,
})(UserDocuments);
