// Tobe replaced with Emmy's function
export const DocumentInArray = (
  docCategories: DocumentReducerInterface,
  DocumentsData: CandidatesInterface[],
  dataType: "all" | "default" = "default"
): TableCandidatesInterface[] | [] => {
  let documentsTemp: TableCandidatesInterface[] = [];
  for (let c in DocumentsData) {
    let details = DocumentsData[c].details;
    for (let d in details) {
      let candidate_detail: CandidateDetailsInterface =
        DocumentsData[c].details[d];
      if (
        candidate_detail.files !== null &&
        candidate_detail.files.length > 0 &&
        dataType === "default" &&
        candidate_detail.validation_status === "waiting"
      ) {
        documentsTemp.push({
          // DATA FROM MAIN PROPERTY
          user_id: DocumentsData[c]._id,
          n_id: DocumentsData[c].n_id,
          fname: DocumentsData[c].user_info.fname,
          lname: DocumentsData[c].user_info.lname,
          phone_number: DocumentsData[c].phone_number,
          district: DocumentsData[c].user_info.district,
          email: DocumentsData[c].email,
          // NEW DATA GENERATED BAISED ON DETAILS PROPERTY
          // main_doc_title: GetDocTitle(
          //   documentCategories,
          //   candidate_detail
          // ).main,
          main_doc_title: GetDocTitle(docCategories, candidate_detail).main,
          // DATA FROM DETAILS PROPERTY
          category_id: candidate_detail.category_id,
          sub_category_id: candidate_detail.sub_category_id,
          category_subcategory_data: candidate_detail.category_subcategory_data,
          title: candidate_detail.title,
          description: candidate_detail.description,
          files: candidate_detail.files,
          start_year: candidate_detail.start_year,
          end_year: candidate_detail.end_year,
          school: candidate_detail.school,
          details_email: candidate_detail.email,
          names: candidate_detail.names,
          details_phone_number: candidate_detail.phone_number,
          company: candidate_detail.company,
          year: candidate_detail.year,
          doc_id: candidate_detail.doc_id,
          validation_status: candidate_detail.validation_status,
          validated_by: candidate_detail.validated_by,
          doc_history_status: candidate_detail.doc_history_status,
          get_in_date: candidate_detail.get_in_date,
          approved: candidate_detail.approved,
          // DATA FROM EXAMS PROPERTY
          exams: DocumentsData[c].exams,
        });
      } else if (
        candidate_detail.files !== null &&
        candidate_detail.files.length > 0 &&
        dataType === "all"
      ) {
        documentsTemp.push({
          // DATA FROM MAIN PROPERTY
          user_id: DocumentsData[c]._id,
          names:
            DocumentsData[c].user_info.fname +
            " " +
            DocumentsData[c].user_info.lname,
          n_id: DocumentsData[c].n_id,
          fname: DocumentsData[c].user_info.fname,
          lname: DocumentsData[c].user_info.lname,
          phone_number: DocumentsData[c].phone_number,
          district: DocumentsData[c].user_info.district,
          email: DocumentsData[c].email,
          main_doc_title: GetDocTitle(docCategories, candidate_detail).main,
          // DATA FROM DETAILS PROPERTY
          category_id: candidate_detail.category_id,
          sub_category_id: candidate_detail.sub_category_id,
          category_subcategory_data: candidate_detail.category_subcategory_data,
          title: candidate_detail.title,
          description: candidate_detail.description,
          files: candidate_detail.files,
          start_year: candidate_detail.start_year,
          end_year: candidate_detail.end_year,
          school: candidate_detail.school,
          details_email: candidate_detail.email,
          // names: candidate_detail.names,
          details_phone_number: candidate_detail.phone_number,
          company: candidate_detail.company,
          year: candidate_detail.year,
          doc_id: candidate_detail.doc_id,
          validation_status: candidate_detail.validation_status,
          validated_by: candidate_detail.validated_by,
          doc_history_status: candidate_detail.doc_history_status,
          get_in_date: candidate_detail.get_in_date,
          approved: candidate_detail.approved,
          // DATA FROM EXAMS PROPERTY
          exams: DocumentsData[c].exams,
        });
      }
    }
  }
  return documentsTemp;
};

export const isDocumentValidationOpen = (
  data: ApplicationSession | null
): boolean => {
  if (data && data.plans) {
    let checkSession = data.plans.find(
      (item) => item.type_key === SESSION_PLAN_KEYS.DOCUMENT_VALIDATION
    );
    if (checkSession) {
      let today = moment(),
        startData = moment(checkSession.start_date),
        endData = moment(checkSession.end_date);
      if (
        startData.diff(today, "days") <= 0 &&
        endData.diff(today, "days") >= 0 &&
        checkSession.type_status === SESSION_PLAN_STATUS.OPEN
      ) {
        return false;
      }
    }
  }
  return true;
};

export const FC_documentFilterByDistrict = (
  candidatesInInstitute: CandidatesInterface[] | null,
  roomTables: LocationRoomTableInterface[],
  filterValue: string
): CandidatesInterface[] => {
  let centersIdInDistrict: string[] = [];
  let districtData: LocationRoomTableInterface[] =
      filterValue === "all"
        ? roomTables
        : roomTables.filter((d) => d.district_id === filterValue),
    l: number = districtData.length,
    i: number;

  // GETTING ALL CENTERS IN SELECTED DISTRICT ================================
  for (i = 0; i < l; i++) {
    if (centersIdInDistrict.includes(districtData[i].center_id)) continue;
    centersIdInDistrict.push(districtData[i].center_id);
  }
  // END GETTING CENTERS IN SELECTED DISTRICT ===================================

  // GETTING LIST OF CANDIDATES IN DISTRICT =========================
  let data: CandidatesInterface[] = [];

  // GET ALL CANDIDATE(WITH DIPLICATION BCAUSE OF APPLICATIONS) WITH DETAILS PROPERTIES =====================================
  let list_details: CandidatesInterface[] = candidatesInInstitute
    ? candidatesInInstitute.filter((candidate) => candidate.details.length > 0)
    : [];

  // GET ALL CANDIDATE(WITH DIPLICATION BCAUSE OF APPLICATIONS) WITH FILES IN DETAILS PROPERTIES =====================================
  let list_files: CandidatesInterface[] = list_details.filter((candidate) =>
    candidate.details.filter((item) => item.files !== "")
  );

  // GET ALL CANDIDATE(REMOVE DIPLICATION CAUSED BY APPLICATIONS) WITH DETAILS PROPERTIES =====================================
  let mailList: string[] = [];
  let list_distinct: CandidatesInterface[] = [];
  for (let i = 0; i < list_files.length; i++) {
    if (mailList.includes(list_files[i].email)) continue;
    mailList.push(list_files[i].email);
    list_distinct.push(list_files[i]);
  }

  let result = data; // restaring list
  for (let i = 0; i < list_distinct.length; i++) {
    for (let j = 0; j < list_distinct[i].exams.length; j++) {
      if (centersIdInDistrict.includes(list_distinct[i].exams[j].center_id)) {
        data.push(list_distinct[i]);
        // action = false;
      }
      // ======================================
      let dataMailList: string[] = [];
      let _data: CandidatesInterface[] = [];
      for (let i = 0; i < data.length; i++) {
        if (dataMailList.includes(data[i].email)) continue;
        dataMailList.push(data[i].email);
        _data.push(data[i]);
      }
      result = _data;
      // ======================================
    }
  }
  return result;
};

