export const getDocByDocId = (
  userList: CandidatesInterface[],
  user_id: string,
  doc_id: string
): {
  user: CandidatesInterface | undefined;
  doc: CandidateDetailsInterface | undefined;
} => {
  let user: CandidatesInterface | undefined = userList.find(
    (user) => user._id === user_id
  );
  let doc: CandidateDetailsInterface | undefined;
  if (user) {
    doc = user.details.find((doc) => doc.doc_id === doc_id);
  }

  return { user, doc };
};

export const countDocumentByStatus = (
  userDocs: CandidateDetailsInterface[],
  docStatus: "waiting" | "approved" | "rejected"
): number => {
  let result: CandidateDetailsInterface[] = userDocs;
  result = userDocs.filter(
    (details) => details.validation_status === docStatus && details.files
  );
  return result.length;
};

export const getDocumentFileByIndex = (
  documentList: TableCandidatesInterface[],
  thisIndex: number,
  setViewDoc: (state: boolean) => void,
  setDocumentId: (state: string) => void,
  setCandidateId: (state: string) => void,
  setDocument_index: (state: number | null) => void,
  setLoadingImage: (state: boolean) => void
) => {
  let n: number = 0,
    found: boolean;
  if (documentList.length <= 0) {
    setViewDoc(false);
    setDocumentId("");
    setCandidateId("");
    setDocument_index(null);
    setLoadingImage(false);
    return false;
  } else if (documentList.length - 1 < thisIndex) {
    n = 0;
    found = false;
    while (!found && documentList.length - 1 >= n) {
      if (
        documentList[n] !== undefined &&
        !isEmptyOrSpaces(documentList[n].files) &&
        !found
      ) {
        setViewDoc(true);
        setDocumentId(documentList[n].doc_id);
        setCandidateId(documentList[n].user_id);
        setDocument_index(n);
        setLoadingImage(true);
        found = true;
      }
      n++;
    }
    if (found) {
      return true;
    } else {
      setViewDoc(false);
      setDocumentId("");
      setCandidateId("");
      setDocument_index(null);
      setLoadingImage(false);
      return true;
    }
  } else {
    // result = documentList[thisIndex];
    if (
      documentList[thisIndex] !== undefined &&
      !isEmptyOrSpaces(documentList[thisIndex].files)
    ) {
      setViewDoc(true);
      setDocumentId(documentList[thisIndex].doc_id);
      setCandidateId(documentList[thisIndex].user_id);
      setDocument_index(thisIndex);
      setLoadingImage(true);
      return true;
    } else {
      n = 0;
      found = false;
      while (!found && documentList.length - 1 >= n) {
        if (
          documentList[n] !== undefined &&
          !isEmptyOrSpaces(documentList[n].files) &&
          !found
        ) {
          setViewDoc(true);
          setDocumentId(documentList[n].doc_id);
          setCandidateId(documentList[n].user_id);
          setDocument_index(n);
          setLoadingImage(true);
          found = true;
        }
        n++;
      }
      if (found) {
        return true;
      } else {
        setViewDoc(false);
        setDocumentId("");
        setCandidateId("");
        setDocument_index(null);
        setLoadingImage(false);
        return true;
      }
    }
  }
};

export const getCandidateFileByIndex = (
  thisCandidate: CandidatesInterface,
  thisIndex: number,
  setViewDoc: (state: boolean) => void,
  setDocumentId: (state: string) => void,
  setCandidateId: (state: string) => void,
  setDocument_index: (state: number | null) => void,
  setLoadingImage: (state: boolean) => void
) => {
  if (thisCandidate.details.length <= 0) {
    setViewDoc(false);
    setDocumentId("");
    setCandidateId("");
    setDocument_index(null);
    setLoadingImage(false);
    return false;
  } else {
    let filesList = thisCandidate.details.filter(
      (details) => details.validation_status === "waiting" && details.files
    );
    if (filesList.length <= 0) {
      setViewDoc(false);
      setDocumentId("");
      setCandidateId("");
      setDocument_index(null);
      setLoadingImage(false);
      return false;
    } else if (filesList.length - 1 < thisIndex) {
      setViewDoc(true);
      setDocumentId(filesList[0].doc_id);
      setCandidateId(thisCandidate._id);
      setDocument_index(0);
      setLoadingImage(true);
      return true;
    } else {
      setViewDoc(true);
      setDocumentId(filesList[thisIndex].doc_id);
      setCandidateId(thisCandidate._id);
      setDocument_index(thisIndex);
      setLoadingImage(true);
      return true;
    }
  }
};

export const countFileByCandidateId = (
  userDocs: CandidateDetailsInterface[],
  filterType: "docCategory" | "docSubCategory" | "docStatus",
  filterValue: string,
  filterStatus: "waiting" | "approved" | "rejected" = "waiting"
): number => {
  let result: CandidateDetailsInterface[] = userDocs;
  if (filterType === "docCategory") {
    result = userDocs.filter(
      (details) =>
        details.category_id === filterValue &&
        details.files &&
        details.files.length > 0 &&
        details.validation_status === filterStatus
    );
  } else if (filterType === "docSubCategory") {
    result = userDocs.filter(
      (details) =>
        details.sub_category_id === filterValue &&
        details.files &&
        details.files.length > 0 &&
        details.validation_status === filterStatus
    );
  }
  return result.length;
};
